
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'

import UtilMixin from '@/mixins/UtilMixin.vue'
import FormMixin from '@/mixins/FormMixin.vue'

export default {
mixins: [UtilMixin, FormMixin],
props: ['identification'],
data() {
return {

tel: '',

code: '',

codeTime: 0
}
},
components: {
XInput,
Group,
XButton,
Box,
Countdown
},
computed: {},
methods: {
sendCode() {
if (this.codeTime !== 0) {
this.$vux.toast.text(`还有${this.codeTime}秒才能再次发送验证码!`)
return
}
if (this.trim(this.tel) === '') {
this.$vux.toast.text('手机号码不能为空')
return
}
this.$axios.post({
url: '/telcheck/',
data: {
tel: this.tel
},
isloadding: true
}).then(res => {
this.$vux.toast.show({
text: '短信已发送',
time: 500
})
this.codeTime = 60
})
},

nextFocus(ref) {
this.$refs[ref].focus()
},
submit() {
this.formMixin_submit('/h5register/')
.then((result) => {
localStorage.setItem('yunhu!customer_id', result.customer_id)
const _this = this
this.$vux.toast.show({
text: '登陆成功',
time: 500,
onHide() {
_this.routerLink('Index', {identification: _this.identification}, {checkway: _this.$route.query.checkway})
}
})
})
.catch((err) => {
console.log(`code:${err.code} \n msg:${err.msg}`)
})
}
},
mounted() {
}
}



